.c-btn {
  display: inline-block;
  background: #0057ff;
  border: 1px solid #0057ff;
  color: white;
  border-radius: 3px;
  font-weight: 400;
  font-size: 1.4em;
  padding: 1.14286em 1em 1.14286em;
  flex: 0 1 14.28571em;
  text-align: center;
  transition-duration: 0.3s;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  /*&:after{
		content: '';
		position: absolute;
		height: 100%;
		width: 100%;
		top:0;
		left:0;
		background: rgba(255,255,255,0.2);
		transform: rotate(25deg) translate(50%,-250%);
		transition-duration: 0.3s;
	}*/ }
  .c-btn:hover {
    background: rgba(255, 255, 255, 0.2);
    color: #0057ff; }
    .c-btn:hover:after {
      transform: none; }
  .c-btn--invert {
    background: transparent; }
    .c-btn--invert:hover {
      color: white;
      background: #0057ff; }
  .c-btn--attantion {
    background: #ffcd00;
    border-color: #ffcd00; }
    .c-btn--attantion:hover {
      color: #ffcd00; }
  .c-btn--warning {
    background: #ff181f;
    border-color: #ff181f; }
    .c-btn--warning:hover {
      color: #ff181f; }

.c-input {
  font-size: 1.4em;
  color: #808080;
  text-indent: 2.5rem;
  min-width: 0;
  transition-duration: 0.3s;
  height: 3.57143em;
  border: 1px solid #0057ff;
  border-radius: 3px; }
  .c-input:focus {
    border-color: #ff181f; }

.text-content {
  color: #333333;
  font-size: 1.4em;
  line-height: 1.42857em;
  /*ol ul*/
  /*/ol ul*/ }
  .text-content *::selection {
    color: #0057ff;
    background: rgba(0, 87, 255, 0.2);
    text-decoration: underline; }
  .text-content a {
    color: #0057ff;
    transition-duration: 0.3s; }
    .text-content a::selection {
      color: #ff181f;
      background: rgba(255, 24, 31, 0.2); }
    .text-content a:hover {
      color: #ff181f; }
  .text-content p:not(:last-child) {
    margin-bottom: 1em; }
  .text-content ul, .text-content ol {
    padding-left: 1.42857em; }
    .text-content ul:not(:first-child), .text-content ol:not(:first-child) {
      margin-top: 1em; }
    .text-content ul:not(:last-child), .text-content ol:not(:last-child) {
      margin-bottom: 1em; }
  .text-content ul li {
    list-style: none;
    position: relative; }
    .text-content ul li:before {
      content: '';
      position: absolute;
      top: 0.71429em;
      left: -1.42857em;
      height: 1px;
      width: 0.85714em;
      background: #69392e; }
  .text-content h1:not(:first-child), .text-content h2:not(:first-child), .text-content h3:not(:first-child), .text-content h4:not(:first-child), .text-content h5:not(:first-child), .text-content h6:not(:first-child) {
    margin-top: 1.5em; }
  .text-content h1:not(:last-child), .text-content h2:not(:last-child), .text-content h3:not(:last-child), .text-content h4:not(:last-child), .text-content h5:not(:last-child), .text-content h6:not(:last-child) {
    margin-bottom: .5em; }
  .text-content blockquote {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1em 1.5rem; }
    .text-content blockquote:not(:first-child) {
      margin-top: 1em; }
    .text-content blockquote:not(:last-child) {
      margin-bottom: 1em; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none; }

a {
  text-decoration: none; }

html {
  height: 100%; }

body {
  max-width: 1920px;
  margin: auto;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: column;
  height: 100%;
  font-size: 1rem; }

.wrapper {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

